









































import { Component, Vue, Watch } from "vue-property-decorator";
import { Wooden } from "@/mechanism/mob/Wooden";

@Component({})
export default class TargetEditor extends Vue {
  type = "";
  lvl = 0;
  p = 1.0;

  @Watch("p")
  @Watch("type")
  @Watch("lvl")
  onChanged() {
    if (this.type == "Wooden")
      this.$emit("input", new Wooden(this.lvl, this.p));
  }
}
